<template>
  <Card class="table-card" title="Latest Attestation">
    <div class="table__wrapper">
      <table>
        <tr>
          <th>Hash</th>
          <th>Status</th>
          <th>Block</th>
          <th>Fee</th>
          <th>Created Time</th>
        </tr>
        <tr v-for="(item, index) in list" :key="index">
          <td @click="open(item.tx_hash)">{{ formatLongStr(item.tx_hash) }}</td>
          <td>
            <span v-if="item.confirm_flag===1" class="confirmed">Confirmed</span>
            <span v-else-if="item.confirm_flag===0" class="failed">Failed</span>
          </td>
          <td>{{ item.block_height }}</td>
          <td>{{ item.fee }} ONG</td>
          <td>{{ item.tx_time | formatDate }}</td>
        </tr>
      </table>
    </div>
  </Card>
</template>

<script>
import Card from '@/components/Card.vue';
import { getUTCTime, openLink } from '@/util';

export default {
  name: 'TableCard',
  components: { Card },
  props: {
    hash: {
      type: String,
      require: true,
    },
    list: {
      type: Array,
      require: true,
    },
  },
  filters: {
    formatDate(val) {
      return getUTCTime(val * 1000);
    },
  },
  methods: {
    formatLongStr(val, mid = '...', start = 8, end = 8) {
      if (val.length > start + end) {
        return `${val.substr(0, start)}${mid}${val.substr(val.length - end, end)}`;
      }
      return val;
    },
    open(hash) {
      openLink(`https://explorer.ont.io/transaction/${hash}`);
    },
  },
};
</script>

<style lang="scss" scoped>
  .table__wrapper {
    overflow: auto;
  }

  .table-card {
    padding-bottom: .4rem;
  }

  table {
    margin-top: .4rem;
    font-size: .28rem;
    border-collapse: collapse;
    overflow: auto;
    white-space: nowrap;

    th, td {
      padding: 0 1rem 0 0;
    }

    th {
      text-align: left;
      line-height: 1.08rem;
      color: var(--color-content);
      font-weight: 500;
      font-size: .24rem;
    }

    td {
      line-height: .88rem;
    }

    .confirmed{
      color: #609A22;
