<template>
  <div class="process">
    <div class="process__box">
      <div class="process__active" :style="{width: getPercent}"></div>
    </div>
    <div class="process__split">
      <div>
        <span class="left">0M</span>
        <span>0.5M</span>
      </div>
      <div><span>1.0M</span></div>
      <div><span>1.5M</span></div>
      <div><span>2.0M</span></div>
      <div><span class="right">2.5M</span></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Process',
  props: {
    count: {
      type: Number,
      require: true,
    },
  },
  computed: {
    getPercent() {
      const rate = this.count / 2500000;
      return rate > 1 ? '100%' : `${rate * 100}%`;
    },
  },
};
</script>

