<template>
  <div class="info-view">
    <Card class="l-margin--first count" title="Attested Data Count" :link="link">
      <div class="count__num">{{ attestedDataCount.toLocaleString() }}</div>
      <span class="dot"></span>
      <span class="label">Existing data</span>
      <Process :count="attestedDataCount" />
    </Card>
    <Card class="l-margin hash" title="Contract Hash">
      <div class="hash__content" @click="openContract()">{{ info.hash }}</div>
    </Card>

    <Card class="l-margin attested" title="Attested Data Record per Transaction">
      <div class="attested__num">{{ perCount }}</div>
    </Card>
    <TableCard class="l-margin" :hash="info.hash" :list="transaction" />
  </div>
</template>

<script>
import Card from "@/components/Card.vue";
import Process from "@/components/Process.vue";
import TableCard from "@/components/TableCard.vue";
import axios from "axios";
import { getPerCount, openLink } from "@/util";

export default {
  name: "InfoView",
  components: {
    TableCard,
    Process,
    Card
  },
  props: {
    info: {
      type: Object,
      require: true
    }
  },
  data() {
    return {
      transaction: [],
      attestedDataCount: 0,
      perCount: 0
    };
  },
  computed: {
    link() {
      return `https://explorer.ont.io/contract/other/${this.info.hash}/10/1/${this.netType}`;
    },
    netType() {
      return this.$route.query.netType === "testnet" ? "testnet" : "";
    },
    explorerLink() {
      return this.$route.query.netType === "testnet"
        ? "https://polarisexplorer.ont.io"
        : "https://explorer.ont.io";
    },
    dappLink() {
      return this.$route.query.netType === "testnet"
        ? "https://polaris1.ont.io:10334"
        : "https://dappnode1.ont.io:10334";
    }
  },
  methods: {
    getTransaction(page = 1) {
      axios
        .get(
          `${this.explorerLink}/v2/contracts/other/${this.info.hash}/transactions?page_size=10&page_number=${page}`
        )
        .then(res => {
          if (res.data && res.data.code === 0 && res.data.result) {
            const list = res.data.result.records || [];
            if (page === 1) {
              this.transaction = list; // save first page list
            }
            const first = list.find(item => item.confirm_flag === 1);
            if (first) {
              this.getAttestedDataCount(first.tx_hash);
              this.getPerAttested(first.tx_hash);
            } else {
              this.getTransaction(page + 1);
            }
          }
        });
    },
    getAttestedDataCount(hash) {
      axios.get(`${this.dappLink}/api/v1/smartcode/event/txhash/${hash}`).then(res => {
        if (res.data && res.data.Desc === "SUCCESS") {
          // eslint-disable-next-line prefer-destructuring
          this.attestedDataCount = Number(res.data.Result.Notify[0]?.States[1] || 0);
        }
      });
    },
    getPerAttested(hash) {
      axios.get(`${this.dappLink}/api/v1/transaction/${hash}?raw=1`).then(res => {
        if (res.data && res.data.Desc === "SUCCESS") {
          this.perCount = getPerCount(res.data.Result);
        }
      });
    },
    openContract() {
      openLink(this.link);
    }
  },
  created() {
    this.getTransaction();
  }
};
</script>

