<template>
  <div class="card">
    <div @click="open">
      <img v-if="link" class="card__arrow" src="../assets/arrow.svg" alt="">
      <h2 class="card__title">{{ title }}</h2>
    </div>
    <slot />
  </div>
</template>

<script>

import { openLink } from '@/util';

export default {
  name: 'Card',
  props: {
    title: {
      type: String,
      require: true,
    },
    link: {
      type: String,
      require: false,
      default: '',
    },
  },
  methods: {
    open() {
      if (!this.link) {
        return;
      }
      openLink(this.link);
    },
  },
};
</script>

<style lang="scss" scoped>
  .card {
    padding: .5rem .4rem 0 .4rem;
    box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.06);
    border-radius: 20px;
  }

  .card__arrow {
    float: right;
    margin-top: .47rem;
    width: .2rem;
  }

  .card__title {
    font-size: .4rem;
    line-height: .63rem;
    font-weight: bold;
    font-family: NoumenaFont-Bold, PingFang SC, Microsoft YaHei, SimHei, sans-serif;
  }
</style>
